@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.TopicPage {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: first;
  justify-content: center;
  font-size: 3rem;
}
.navbar {
    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: white;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #1a83ff;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span {
    margin-left: 16px;

}
.NewsArticle {
    width: 100%;
    padding-top: 20px;
    align-items: center;
    justify-content: flex-end;
    padding-right: 60px;
    padding-left: 20px;
    display: flex;
}

.MainNewsArticle {
    width: 100%;
    padding-top: 20px;
    align-items: center;
    justify-content: flex-end;
    padding-right: 40px;
    padding-left: 20px;
    display: flex;
}

.NewsArticleWithLink a{
    color: black;
    text-decoration: none;
}

.NewsArticleWithLink a:hover {
    color: black;
    text-decoration: underline;
}

.articleNewspaper {
    display: flex;
    justify-content: flex-end;
}

.articleNewspaper img {
    margin-left: 10px;
    margin-top: 4px;
    border-radius: 1px;
    height: 16px;
    width: 16px;
}
.newsClusters {
    border: 1px solid black;
    border-radius: 15px;
    margin-bottom: 20px;
}
.button-full-coverage {
    padding-right: 30px;
    padding-bottom: 20px;
    display: flex;
    justify-content: flex-end;

}

.button-full-coverage button {
    -webkit-appearance: button;
    -webkit-writing-mode: horizontal-tb !important;
    text-rendering: auto;
    color: buttontext;
    letter-spacing: normal;
    word-spacing: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    display: inline-block;
    text-align: center;
    align-items: flex-start;
    cursor: default;
    background-color: buttonface;
    box-sizing: border-box;
    margin: 0em;
    font: 400 18px system-ui;
    padding: 1px 7px 2px;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(216, 216, 216) rgb(141, 137, 137) rgb(186, 186, 186);
    border-image: none;
    border-image: initial;
}
.topicTitle {
    display: flex;
    justify-content: center;
    padding-top: 15px;
    padding-bottom: 20px;
}

.NewsTopics {
    display: flex;
    justify-content: center;
    align-items: center;
}
